import React from 'react'
import axios from 'axios';


import {Button, Descriptions, Space,  Modal,  Tooltip, Tag} from 'antd';
import {ExclamationCircleOutlined, AppstoreOutlined, DeleteOutlined} from '@ant-design/icons'

import Notifications from '../../../commons/Notifications'

function Item(props) {
    const [row, setRow] = React.useState(null);

    const handleList = () => {
        props.setView('list')
        props.setNo(null);
        window.scrollTo(0, 0)
    }

    const handleRemove = () => {
        Modal.confirm({
            title: '?',
            icon: <ExclamationCircleOutlined />,
            content: '   .',
            okText: '',
            okType: 'danger',
            cancelText: '',
            onOk() {
                const newData = props.dataSource.filter( x => x.no !== props.no);
                axios.delete(`/api/orders/${props.no}`).then(response => {
                    if(response.data.success) {
                        props.setDataSource(newData);
                        props.setView('list')
                        props.setNo(null);
                        window.scrollTo(0, 0)
                    }
                    Notifications(response.data)
                })                
            },
            onCancel() {
                return false;
            }
        });
    }

    const fetchData = async () => {
        const result = await (await axios.get(`/api/orders/${props.no}`)).data.data
        setRow(JSON.parse(result.data));
    }

    React.useEffect(() => {
     fetchData();       
    }, [])

    return (
        <>
            <div id="admin-cases-item-buttons">
                <Space>
                    <Tooltip title="">
                        <Button onClick={()=>handleList()} shape="round" icon={<AppstoreOutlined />} />
                    </Tooltip>
                    <Tooltip title="">
                        <Button onClick={()=>handleRemove()} shape="round" icon={<DeleteOutlined />} />
                    </Tooltip>
                </Space>
            </div>
            {row !== null && (
                <div id="admin-cases-item-container"> 
                    <Descriptions size="small" layout="vertical" bordered className="ant-descriptions-container">
                        <Descriptions.Item label="">{row.name}</Descriptions.Item>
                        <Descriptions.Item label="" span={2}>{row.address}</Descriptions.Item>
                        <Descriptions.Item label="">{row.contact}</Descriptions.Item>
                        <Descriptions.Item label="">{row.email}</Descriptions.Item>
                        <Descriptions.Item label="  ">{row.expected_date.substring(0,10)}</Descriptions.Item>
                        <Descriptions.Item label="  ">{row.expected_price}</Descriptions.Item>
                        <Descriptions.Item label="">{row.area}</Descriptions.Item>
                        <Descriptions.Item label="">{row.papering.map((v,i) => <Tag key={i} color="default">{v}</Tag>)}</Descriptions.Item>
                        <Descriptions.Item label="">{row.flooring.map((v,i) => <Tag key={i} color="default">{v}</Tag>)}</Descriptions.Item>
                        <Descriptions.Item label=" ">{row.finishing_material.map((v,i) => <Tag key={i} color="default">{v}</Tag>)}</Descriptions.Item>
                        <Descriptions.Item label=" ">{row.light.map((v,i) => <Tag key={i} color="default">{v}</Tag>)}</Descriptions.Item>
                        <Descriptions.Item label=" ">{row.furniture.map((v,i) => <Tag key={i} color="default">{v}</Tag>)}</Descriptions.Item>
                        <Descriptions.Item label=" ">{row.tile.map((v,i) => <Tag key={i} color="default">{v}</Tag>)}</Descriptions.Item>
                        <Descriptions.Item label=" ">{row.bathroom.map((v,i) => <Tag key={i} color="default">{v}</Tag>)}</Descriptions.Item>
                        <Descriptions.Item label=" ">{row.expansion.map((v,i) => <Tag key={i} color="default">{v}</Tag>)}</Descriptions.Item>
                        <Descriptions.Item label=" ">{row.chassis.map((v,i) => <Tag key={i} color="default">{v}</Tag>)}</Descriptions.Item>
                        <Descriptions.Item label="">{row.woodwork.map((v,i) => <Tag key={i} color="default">{v}</Tag>)}</Descriptions.Item>
                        <Descriptions.Item label="">{row.artwall.map((v,i) => <Tag key={i} color="default">{v}</Tag>)}</Descriptions.Item>
                        <Descriptions.Item label=" ">{row.etc.map((v,i) => <Tag key={i} color="default">{v}</Tag>)}</Descriptions.Item>
                        <Descriptions.Item label=" ">{row.styling.map((v,i) => <Tag key={i} color="default">{v}</Tag>)}</Descriptions.Item>
                        <Descriptions.Item label=" ">{row.qna}</Descriptions.Item>                       
                    </Descriptions>
                </div>
            )}
        </>
    )
}

export default React.memo(Item)
