import React from 'react'
import axios from 'axios'
import { Form, Select, Button, Input, Tooltip, DatePicker, Row, Col, Modal, message } from 'antd';
import {QuestionCircleOutlined, ExclamationCircleOutlined} from '@ant-design/icons'
import DaumPostCode from 'react-daum-postcode';

const width = 700;
const height = 450;
const modalStyle = {
    overflow: "hidden"
}

function OrderPage() {
    const [address, setAddress] = React.useState('');
    const [visible, setVisible] = React.useState(false)
    const formItemLayout = {
      labelCol: {
        span: 9,
      },
      wrapperCol: {
        span: 10,
      },
    };    

    const handleAddress = (data) => {
        let AllAddress = data.address;
        let extraAddress = ''; 
        
        if (data.addressType === 'R') {
          if (data.bname !== '') {
            extraAddress += data.bname;
          }
          if (data.buildingName !== '') {
            extraAddress += (extraAddress !== '' ? `, ${data.buildingName}` : data.buildingName);
          }
          AllAddress += (extraAddress !== '' ? ` (${extraAddress})` : '');
        }
        setAddress(AllAddress)
        setVisible(false);
    }

    const handleOpenPost = () => {
        setVisible(true)
    };

    const onFinish = (values) => {
        values['address'] = address;
        axios.post('/api/orders', values).then((response) => {
            if(response.data.success) {
                message.success(response.data.msg)
                // need redirecting 
            } else {
                console.log(false)
            }
        })
    };
    
    return (
        <div className='user-order-container'>
            <h2></h2>

            <div className='user-order-content'>
                <Form name="validate_other" {...formItemLayout} onFinish={onFinish} requiredMark={false} size='large'>
                    <Form.Item name="name" hasFeedback wrapperCol={{span: 7}} label={ <span></span>} 
                    rules={[{required: true, message: ' ', whitespace: true}]}>
                        <Input style={{ width: '100%' }}/>
                    </Form.Item>

                    <Form.Item name="contact" hasFeedback label='' wrapperCol={{span: 7}}
                                rules={[{required: true, message: ' ', whitespace: true}]}>
                        <Input style={{ width: '100%' }} placeholder='-   ' />
                    </Form.Item>

                    <Form.Item name="email" hasFeedback label='' wrapperCol={{span: 7}}
                        rules={[{ type: 'email', message: '  '}, {required: true, message: ' ', whitespace: true}]}>
                        <Input style={{ width: '100%' }}/>
                    </Form.Item>

                    <Form.Item name="address" label='' wrapperCol={{span: 10}}>
                        <Row gutter={8}>
                            <Col span={23}>
                                <Input value={address} style={{ width: '100%' }} onClick={handleOpenPost} />
                            </Col>
                            <Col span={1}>
                                <Button onClick={handleOpenPost}><span> </span></Button>
                                
                            </Col>
                        </Row>
                    </Form.Item>

                    <Form.Item name="expected_date" hasFeedback label='  '  wrapperCol={{span: 5}}
                        rules={[{required: true, message: ' '}]}>
                        <DatePicker style={{width: '100%'}}/>
                    </Form.Item>

                    <Form.Item name="expected_price" hasFeedback label='  ' wrapperCol={{span: 7}}
                                rules={[{required: true, message: '   '}]}>
                        <Input style={{width: '100%'}} placeholder='   ' /> 
                    </Form.Item>

                    <Form.Item name="area" hasFeedback label="" wrapperCol={{span: 7}} rules={[{required: true, message: ' '}]}>
                        <Select placeholder=" .">
                            <Select.Option value="20">20</Select.Option>
                            <Select.Option value="30">30</Select.Option>
                            <Select.Option value="40">40</Select.Option>
                            <Select.Option value="50">50</Select.Option>
                        </Select>
                    </Form.Item>

                    <Form.Item name="papering" hasFeedback label=""
                                rules={[{required: true, message: ' ', type: 'array'}]}>
                        <Select mode="multiple" placeholder=" .">
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=",  +  ">,  +  </Select.Option>
                        </Select>
                    </Form.Item>
                    
                    <Form.Item name="flooring" hasFeedback label="" 
                                 rules={[{required: true, message: ' ', type: 'array'}]}>
                        <Select mode="multiple" placeholder=" ">
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=",  +  ">,  +  </Select.Option>
                            <Select.Option value=",  +  ">,  +  </Select.Option>
                            <Select.Option value="    ">    </Select.Option>
                        </Select>
                    </Form.Item>

                    <Form.Item name="finishing_material" hasFeedback  label={ <span> &nbsp;&nbsp;
                            <Tooltip title="  : , , ,  ,   ">
                                <ExclamationCircleOutlined />
                            </Tooltip></span>} 
                            rules={[{required: true, message: ' ', type: 'array'}]}>
                        <Select mode="multiple" placeholder=" ">
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value="> "> </Select.Option>
                        </Select>
                    </Form.Item>

                    <Form.Item name="light" hasFeedback label=" " 
                                rules={[{ required: true, message: ' ', type: 'array'}]}>
                        <Select mode="multiple" placeholder=" ">
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=",    ">,    </Select.Option>
                            <Select.Option value="TV ">TV  </Select.Option>
                            <Select.Option value="">  </Select.Option>
                            <Select.Option value=", ">,   </Select.Option>
                            <Select.Option value=""></Select.Option>
                        </Select>
                    </Form.Item>

                    <Form.Item name="furniture" hasFeedback label=" " 
                                rules={[{required: true, message: ' ', type: 'array'}]}>
                        <Select mode="multiple" placeholder=" ">
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                        </Select>
                    </Form.Item>

                    <Form.Item name="tile" hasFeedback label=" " 
                                rules={[{required: true, message: ' ', type: 'array'}]}>
                        <Select mode="multiple" placeholder=" ">
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=" "> </Select.Option>
                        </Select>
                    </Form.Item>

                    <Form.Item name="bathroom" hasFeedback label=" " 
                                rules={[{required: true, message: ' ', type: 'array'}]}>
                        <Select mode="multiple" placeholder=" ">
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                        </Select>
                    </Form.Item>

                    <Form.Item name="expansion" hasFeedback label=" " 
                                rules={[{required: true, message: ' ', type: 'array'}]}>
                        <Select mode="multiple" placeholder=" ">
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                        </Select>
                    </Form.Item>

                    <Form.Item name="chassis" hasFeedback label=" " 
                                rules={[{required: true, message: ' ', type: 'array'}]}>
                        <Select mode="multiple" placeholder=" ">
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value="()"> ()</Select.Option>
                        </Select>
                    </Form.Item>

                    <Form.Item name="woodwork" hasFeedback label="" 
                                rules={[{required: true, message: ' ', type: 'array'}]}>
                        <Select mode="multiple" placeholder=" ">
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=" "> </Select.Option>
                        </Select>
                    </Form.Item>

                    <Form.Item name="artwall" hasFeedback label=""
                                rules={[{required: true, message: ' ', type: 'array'}]}>
                        <Select mode="multiple" placeholder=" ">
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=" "> </Select.Option>
                        </Select>
                    </Form.Item>

                    <Form.Item name="etc" hasFeedback label=" " 
                                rules={[{required: true, message: ' ', type: 'array'}]}>
                        <Select mode="multiple" placeholder=" ">
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=" "> </Select.Option>
                            <Select.Option value=" "> </Select.Option>
                        </Select>
                    </Form.Item>

                    <Form.Item name="styling" hasFeedback label={ <span> &nbsp;
                            <Tooltip title="  , , ,         .">
                                <QuestionCircleOutlined />
                            </Tooltip></span>} 
                            rules={[{required: true, message: '  ', type: 'array'}]}>
                        <Select mode="multiple" placeholder="  ">
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                            <Select.Option value=""></Select.Option>
                        </Select>
                    </Form.Item>
              
                    <Form.Item name='qna' label=" ">
                        <Input.TextArea style={{ width: '100%' }}/>
                    </Form.Item>

                    <Form.Item wrapperCol={{ span: 15, offset: 12 }}>
                        <Button htmlType="submit"></Button>
                    </Form.Item>
                </Form>
                {
                    visible ? (
                        <Modal title=" " 
                                centered 
                                visible={visible}
                                width={750}
                                onCancel={()=>setVisible(false)}
                                footer={null}>
                                
                            <DaumPostCode onComplete={handleAddress} width={width} height={height} style={modalStyle}/>
                        </Modal>
                    ) : null
                }
            </div>
        </div>
    )
}

export default React.memo(OrderPage)

