import React, { useState } from "react";
import { withRouter } from "react-router-dom";
import { loginUser } from "../../../../_actions/user_actions";
import { Formik } from 'formik';
import * as Yup from 'yup';
import { Form, Input, Button, Typography } from 'antd';
import { useDispatch } from "react-redux";
import {UserOutlined , LockOutlined} from '@ant-design/icons';
const { Title } = Typography;

function LoginPage(props) {
  const dispatch = useDispatch();
  const [formErrorMessage, setFormErrorMessage] = useState('')

  return (
    <>
        <Formik initialValues={{ adminId: '', password: ''}}
                validationSchema={Yup.object().shape({ adminId: Yup.string().required('id is required'),
                    password: Yup.string().min(6, 'Password must be at least 6 characters').required('Password is required')})}
                onSubmit={(values, { setSubmitting }) => {
                        setTimeout(() => {
                          let dataToSubmit = {
                            adminId: values.adminId,
                            password: values.password
                          };

          dispatch(loginUser(dataToSubmit))
            .then(response => {
              if (response.payload.loginSuccess) {
                window.localStorage.setItem('userId', response.payload.userId);
                props.history.push("/");
              } else {
                setFormErrorMessage('Check out your Account or Password again')
              }
            })
            .catch(err => {
              setFormErrorMessage('Check out your Account or Password again')
              setTimeout(() => {
                setFormErrorMessage("")
              }, 3000);
            });
          setSubmitting(false);
        }, 500);
      }}
    >
      {props => {
        const {
          values,
          touched,
          errors,
          /* dirty, */
          isSubmitting,
          handleChange,
          handleBlur,
          handleSubmit
          /* handleReset, */
        } = props;
        return (
          <div className="app">

            <Title level={2}>Log In</Title>
            <form onSubmit={handleSubmit} style={{ width: '350px' }}>

              <Form.Item required>
                <Input
                  id="adminId"
                  prefix={<UserOutlined style={{ color: 'rgba(0,0,0,.25)' }} />}
                  placeholder="Enter your id"
                  type="text"
                  value={values.adminId}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  className={
                    errors.adminId && touched.adminId ? 'text-input error' : 'text-input'
                  }
                />
                {errors.adminId && touched.adminId && (
                  <div className="input-feedback" style={{ marginTop: '0rem' }}>{errors.adminId}</div>
                )}
              </Form.Item>

              <Form.Item required>
                <Input
                  id="password"
                  prefix={<LockOutlined style={{ color: 'rgba(0,0,0,.25)' }} />}
                  placeholder="Enter your password"
                  type="password"
                  value={values.password}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  className={
                    errors.password && touched.password ? 'text-input error' : 'text-input'
                  }
                />
                {errors.password && touched.password && (
                  <div className="input-feedback" style={{ marginTop: '0rem' }}>{errors.password}</div>
                )}
              </Form.Item>

              {formErrorMessage && (
                <label ><p style={{ color: '#ff0000bf', fontSize: '0.7rem', border: '1px solid', padding: '1rem', borderRadius: '10px', marginTop: '10px;'}}>{formErrorMessage}</p></label>
              )}

              <Form.Item>
                <div>
                  <Button type="primary" htmlType="submit" className="login-form-button" style={{ minWidth: '100%' }} disabled={isSubmitting} onSubmit={handleSubmit}>
                    Log in
                  </Button>
                </div>
              </Form.Item>
            </form>
          </div>
        );
      }}
    </Formik>
    </>
  );
};

export default withRouter(LoginPage);


